import {Component} from '@angular/core'
import {Router} from '@angular/router'
import {Observable} from 'rxjs/Observable'
import { FileUploader } from 'ng2-file-upload'

import {ProductoService, UtilisService} from '../../../../services'
import {Producto, Linea, Presentaciones} from '../../../../models'
import 'rxjs/add/operator/switchMap'

declare const $:any

@Component({
  moduleId: module.id,
  styleUrls: ['producto.scss'],
  templateUrl: 'producto.html'
})
export class ProductoComponent {
  producto: Producto
  addNewCategory: boolean = false
  productos: Producto[] = []
  productoCreated: boolean = false
  linea: Linea
  lineas$: Observable<Linea[]>
  categorias$: any
  categoria: string = ''
  thumbnail: string = ''

  uploader = new FileUploader({url: `/thumbnail`})

  constructor(
    private productoService: ProductoService,
    private utilsService: UtilisService,
    private router: Router
  ) {
    this.cleanProduct()
    this.getData()
  }

  getData() {
    this.lineas$ = this.productoService.getLineas()
    this.categorias$ = this.utilsService.getCategories()

    this.lineas$.subscribe(l => console.log(l))
  }

  addCategory() {
    this.categorias$
      .subscribe(({categories}) => {
        if (categories.includes(this.categoria)) {
          this.cleanSetCategory()
        } else {
          this.utilsService
            .addCategory(this.categoria)
            .subscribe(
              ({name}) => {
                this.categorias$ = this.utilsService.getCategories()
                this.cleanSetCategory()
              },
              err => console.log(err),
              () => console.log('category created.'))
        }
      })
  }

  createProduct () {
    this.productoService
      .createProducto(this.producto, this.linea._id)
      .subscribe(
        () => {
          this.productos.push(Object.assign({}, this.producto,{linea: this.linea.nombre}))
          this.productoCreated = true
        }, (err) => console.log(err),
        () => console.log('product creation process ended.'))
  }

  newProduct () {
    this.cleanProduct()
    this.getData()
    this.productoCreated = false
    this.addNewCategory = false
  }

  uploadFile() {
    this.uploader.uploadAll()
    this.uploader.onCompleteItem = (item: any, response: any, status: any, headers:any) =>  {
      const {img} = JSON.parse(response)
      this.producto.logo = img
      this.createProduct()
    }
  }

  startUpload(): void {
    if (this.uploader.queue.length > 0) {
      this.uploadFile()
    } else {
      this.createProduct()
    }
  }

  cleanSetCategory () {
    this.producto.categoria = this.categoria
    this.categoria = ''
  }

  cleanProduct (){
    this.thumbnail = 'http://placehold.it/180'
    $('#blah').attr('src', this.thumbnail)
    this.productoCreated = false
    this.uploader.clearQueue()

    this.producto = {
      nombre: '',
      categoria: '',
      activo: true,
      presentaciones: []
    }
  }
}



