import {RouterModule} from '@angular/router';
import {
  ProductoRegistrarMainComponent,
  ProductoRegistrarPanelComponent,
  LineaComponent,
  ProductoComponent,
  PresentacionesComponent
} from './'

const routes = [{
  path: '',
  component: ProductoRegistrarMainComponent,
  children: [{
    path: 'panel',
    component: ProductoRegistrarPanelComponent
    }, {
    path: 'agregar',
    children: [{
      path: 'linea',
      component: LineaComponent
    }, {
      path: 'producto',
      component: ProductoComponent
    }, {
      path: 'presentaciones',
      component: PresentacionesComponent
    }]
  }, {
    path: 'editar',
    children: [{
      path: 'linea/:linea/producto/:producto/presentacion/:presentacion',
      component: PresentacionesComponent
    }]
  }]
}]

export const RegistroProductos = RouterModule.forChild(routes)



