import {Component, EventEmitter, ViewChild, ElementRef} from '@angular/core'
import {Router, ActivatedRoute, Params} from '@angular/router'
import {DomSanitizer} from '@angular/platform-browser'
import {Observable} from 'rxjs/Observable'
import { FileUploader } from 'ng2-file-upload'

import {ProductoService, UtilisService} from '../../../../services'
import {Producto, Linea, Presentaciones} from '../../../../models'

declare const $:any

@Component({
  moduleId: module.id,
  styleUrls: ['presentacion.scss'],
  templateUrl: 'presentacion.html'
})
export class PresentacionesComponent {
  @ViewChild('thumb') thumb: ElementRef

  presentacion: Presentaciones
  presentacionCreated: boolean = false
  presentaciones: Presentaciones[] = []
  lineas$: Observable<Linea[]>
  productos$: Observable<Producto[]>

  linea: string
  producto: string

  productos: Producto[] = []
  thumbnail: string
  productoId: string
  lineaId: string
  presentacionId: string

  edit: boolean = false

  uploader = new FileUploader({url: `/thumbnail`})

  constructor(
    private productoService: ProductoService,
    private utilsService: UtilisService,
    private sanitizer: DomSanitizer,
    private router: Router,
    private route: ActivatedRoute
  ) {
    if (/editar/.test(router.url)) {
      route.params
        .subscribe((params: Params) => {
          this.lineaId = params['linea']
          this.productoId = params['producto']
          this.presentacionId = params['presentacion']
          this.productoService
            .getPresentacion(this.lineaId, this.productoId, this.presentacionId)
            .subscribe(({presentacion: p})=> {
              console.log(p)
              this.linea = p.linea._id
              this.getProductos()
              this.producto = p.productos.id
              for (let [k, v] of Object.entries(p.presentacion)) {
                this.presentacion[k] = v
              }
              this.edit = true
              if (p.imagen !== 'http://placehold.it/180') {
                  $('#blah').attr('src', p.imagen)
              }
            })
        })
    }
  }

  ngOnInit () {
    this.cleanPresentacion()
    this.lineas$ = this.productoService.getLineas()
  }

  getProductos() {
    this.productos$ = this.productoService
      .getProductos(this.linea)
  }

  createPresentacion () {
    if (this.edit) {
      this.productoService
        .updatePresentacion(this.lineaId, this.productoId, this.presentacion)
        .subscribe(
          d => {
            console.log(d)
            this.router.navigate(['', 'user', 'productos', 'lista', 'lineas', this.linea, 'productos', this.producto, 'presentaciones'])
          },
          err => console.log(err),
          () => console.log('presentacion update process ended.')
        )
    } else {
      this.productoService
        .createPresentaciones(this.linea, this.producto, this.presentacion)
        .subscribe(
          ({success}) => {
              this.presentaciones.push(this.presentacion)
              this.presentacionCreated = true
          }, (err) => console.log(err),
          () => console.log('presentacion creation process ended.'))
    }
  }

  newProduct() {
    this.cleanPresentacion()
  }

  uploadFile() {
    this.uploader.uploadAll()
    this.uploader.onCompleteItem = (item: any, response: any, status: any, headers:any) =>  {
      const {img} = JSON.parse(response)
      this.presentacion.imagen = img
      this.createPresentacion()
    }
  }

  startUpload(): void {
    if (this.uploader.queue.length > 0) {
      this.uploadFile()
    } else {
      this.createPresentacion()
    }
  }

  cleanPresentacion () {
    this.thumbnail = 'http://placehold.it/180'
    $('#blah').attr('src', this.thumbnail)
    this.presentacionCreated = false
    this.uploader.clearQueue()
    this.edit = false

    this.presentacion = {
      activo: true,
      codigo: '',
      presentacion: '',
      totalElementosPorCaja: 0,
      pesoTotalPorCaja: 0,
      precioUnitario: 0,
      precioPorCaja: 0,
      descripcion: '',
      imagen: 'http://placehold.it/180'
    }
  }
}



