import {Component, EventEmitter, ViewChild, ElementRef} from '@angular/core'
import {DomSanitizer} from '@angular/platform-browser'
import {Router} from '@angular/router'


import { FileUploader } from 'ng2-file-upload'

import {ProductoService, UtilisService, AuthService} from '../../../../services'
import {Producto, Linea, Presentaciones} from '../../../../models'

declare const $:any

@Component({
  moduleId: module.id,
  styleUrls: ['linea.scss'],
  templateUrl: 'linea.html'
})
export class LineaComponent {
  @ViewChild('thumb') thumb: ElementRef

  linea: Linea
  thumbnail: string
  lineCreated: boolean = false
  lineas: Linea[] = []
  thumbnailExists: boolean = false


  uploader = new FileUploader({url: `/thumbnail`})

  constructor(
    private router: Router,
    private sanitizer: DomSanitizer,
    private productoService: ProductoService,
    private utilsService: UtilisService,
    private authService: AuthService
  ) {}

  ngOnInit () {
    this.cleanLinea()
  }

  createLinea() {
    this.productoService
      .createLinea(this.linea)
      .subscribe(
        ({success}) => {
          this.lineas.push(this.linea)
          this.lineCreated = success
        },
        (err) => {
          if (err.status === 401) {
              this.authService.isAuthorized()
          }
          console.log(err)
        },
        () => console.log('Line creation process ended.')
      )
  }

  uploadFile() {
    this.uploader.uploadAll()
    this.uploader.onCompleteItem = (item: any, response: any, status: any, headers:any) =>  {
      const {img} = JSON.parse(response)
      this.linea.logo = img
      this.createLinea()
    }
  }

  startUpload(): void {
    if (this.uploader.queue.length > 0) {
      this.uploadFile()
    } else {
      this.createLinea()
    }
  }

  newLine() {
    this.cleanLinea()
  }

  cleanLinea () {
    this.lineCreated = false
    this.uploader.clearQueue()
    this.thumbnail = 'http://placehold.it/180'

    $('#blah').attr('src', this.thumbnail)
    this.linea = {
      nombre: '',
      activo: true,
      logo: 'http://placehold.it/180',
      productos: []
    }
  }
}



